@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
.site-header {
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 5.5rem;
  width: var(--safe-content-width);
}

.site-title {
  text-align: right;
}

.site-title a:any-link {
  text-decoration: inherit;
  color: inherit;
}

.navbar {
  text-align: right;
}

.navbar-link:any-link {
  color: var(--color-medium-gray);
  text-decoration: none;
  transition-property: color;
  transition-duration: 0.15s;
  margin-left: 2rem;
}

.navbar-link:hover {
  color: var(--color-medium-blue);
  transition-property: color;
  transition-duration: 0.15s;
}

/* Highlights the current page in the navbar */
.navbar-link.active {
  color: var(--color-medium-blue);
}

/* Mobile */
@media (max-width: 900px) {
  .site-header {
    margin-bottom: 4rem;
  }
}

.about-section {
  margin: auto;
  width: var(--safe-content-width);
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 9rem;
}

.about-photo {
  padding-right: 18%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-text p,
.about-text h3 {
  margin-top: 0;
  margin-bottom: 1rem;
}

/* Desktop-max-width */
@media (min-width: 1480px) {
  .about-photo {
    padding-left: 5%;
  }
}

/* Mobile: single-column */
@media (max-width: 900px) {
  .about-section {
    grid-template-columns: 1fr;
    margin-bottom: 7rem;
  }

  .about-photo {
    padding: 0 10% 4rem;
  }
}

.social-icons {
  display: flex;
  justify-content: flex-end;
  margin-top: 0.625rem;
}

.social-icon {
  display: inline-block;
  background-repeat: no-repeat;
  background-size: contain;
  width: 2rem;
  height: 2rem;
  margin-right: 1.125rem;
  margin-left: 0.25rem;
  transition-duration: 0.2s;
}

#linkedin-icon {
  background-image: url(data:image/png;base64,);
}
#linkedin-icon:hover {
  background-image: url(data:image/png;base64,);
}

#github-icon {
  background-image: url(data:image/png;base64,);
}
#github-icon:hover {
  background-image: url(data:image/png;base64,);
}

#instagram-icon {
  background-image: url(data:image/png;base64,);
}
#instagram-icon:hover {
  background-image: url(/static/media/instagram-icon-color.47a67eca.png);
}

/* Image pre-loading trick -- prevents delay to show color version on hover */
body::after {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  z-index: -1;
  content: url(data:image/png;base64,)
    url(data:image/png;base64,)
    url(/static/media/instagram-icon-color.47a67eca.png);
}

.button:any-link {
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  border-radius: 100em;
  transition-duration: 150ms;

  padding: 0.5em 1em;
}

.button:any-link {
  box-shadow: 0px 0px 0px 1px var(--color-medium-gray) inset;
  color: var(--color-medium-gray);
}

.button:hover {
  box-shadow: 0px 0px 0px 2px var(--color-medium-gray) inset;
  background-color: rgba(var(--color-medium-gray-rgb), 0.08);
}

.button:active {
  box-shadow: 0px 0px 0px 2px var(--color-dark-gray) inset;
  color: var(--color-dark-gray);
}

.button.highlighted:any-link {
  box-shadow: 0px 0px 0px 1px var(--color-medium-blue) inset;
  color: var(--color-medium-blue);
}

.button.highlighted:hover {
  box-shadow: 0px 0px 0px 2px var(--color-medium-blue) inset;
  background-color: rgba(var(--color-medium-blue-rgb), 0.08);
}

.button.highlighted:active {
  box-shadow: 0px 0px 0px 2px var(--color-dark-blue) inset;
  color: var(--color-dark-blue);
}

.project-item .image {
  padding-top: 75%; /* controls image aspect ratio */
  position: relative;
}

.project-item .image * {
  object-fit: cover;
  height: 100%;
  position: absolute;
  top: 0;
}

.project-item .description-title {
  margin-bottom: 0rem;
}

.project-item .description-subtitle {
  margin-bottom: 1.5rem;
}

.project-item .description-body {
  margin-bottom: 2rem;
}

.project-item .buttons-container > * {
  margin-right: 1rem;
}

/* Desktop */
@media (min-width: 901px) {
  .project-item {
    margin: auto;
    width: var(--full-content-width);
    display: flex;
    align-items: center;
    margin-bottom: 7rem;

    --description-margin: 20%;
  }

  .project-item .description-column {
    flex: 1 1;
  }

  .project-item .image-column {
    flex: 1.25 1;
  }

  .project-item.image-left {
    flex-direction: row-reverse;
  }

  .project-item.image-right .description-column {
    margin-left: var(--safe-content-margin);
  }

  .project-item.image-right .description {
    margin-right: var(--description-margin);
  }

  .project-item.image-left .description-column {
    margin-right: var(--safe-content-margin);
  }

  .project-item.image-left .description {
    margin-left: var(--description-margin);
  }
}

/* Mobile */
@media (max-width: 900px) {
  .project-item {
    margin: auto;
    width: var(--safe-content-width);
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 6rem;
  }

  .project-item .image-column {
    margin-bottom: 1.5rem;
  }
}

.section-header {
  margin: auto;
  width: var(--full-content-width);
  margin-bottom: 3rem;
}

.section-header .line {
  width: auto;
  height: 0.5rem;
  background-color: var(--color-light-gray);
  border-radius: 2px;
  margin-left: calc(var(--safe-content-margin) * 0.5);
}

.section-header .title {
  margin-left: calc(var(--safe-content-margin) * 0.6);
  line-height: 1em;
}

.resume-page {
  margin: auto;
  width: var(--safe-content-width);
  margin-bottom: var(--bottom-page-margin);
}

.resume-pdf-container {
  position: relative;
  display: block;
  padding-top: 137%;
}

.resume-pdf {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
}

:root {
  --desktop-max-width: 1480px;
  --full-content-width: min(var(--desktop-max-width), 95%);
  --safe-content-width: calc(var(--full-content-width) * 0.8);
  --safe-content-margin: calc(
    (var(--full-content-width) - var(--safe-content-width)) / 2
  );
  --bottom-page-margin: 4rem;

  --default-border-radius: 4px;
  --color-light-gray: #dedede;
  --color-medium-gray: #727272;
  --color-medium-gray-rgb: 114, 114, 114;
  --color-dark-gray: #333333;
  --color-medium-blue: #3484bd;
  --color-medium-blue-rgb: 52, 132, 189;
  --color-dark-blue: #235980;
}

.social-icons {
  display: flex;
  justify-content: flex-end;
  margin-top: 0.625rem;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  color: #333;
}

img,
video {
  width: 100%;
  border-radius: var(--default-border-radius);
}

h1 {
  font-size: 4.5rem;
  font-weight: 600; /* Semibold */
  color: var(--color-medium-gray);
  margin: 0;
}

h2 {
  font-size: 2.75rem;
  font-weight: 600;
  color: var(--color-medium-gray);
  margin: 0;
}

h3 {
  font-size: 2rem;
  font-weight: 600;
  color: var(--color-dark-gray);
  margin: 0;
}

h4 {
  font-size: 1.5rem;
  font-weight: 400;
  color: var(--color-dark-gray);
  margin: 0;
}

/* default link styling */
a:any-link {
  color: var(--color-medium-blue);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* selected link */
a:active {
  color: var(--color-dark-blue);
}

/* Mobile */
@media (max-width: 900px) {
  :root {
    --full-content-width: 90%;
    --safe-content-width: var(--full-content-width);
  }

  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.25rem;
  }

  h3 {
    font-size: 1.75rem;
  }

  h4 {
    font-size: 1.25rem;
  }
}

